<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "app",
  created() {
    const id = this.$route.query.id;
    if (id) {
      this.$store.commit("setCurrentDeviceId", id);
    }

    const lang = this.$route.query.lang;
    if (lang) {
      let lang_;
      if (lang.toLowerCase().startsWith("zh")) {
        lang_ = "zh";
      } else if (!["zh", "ja", "en"].includes(lang)) {
        lang_ = "en";
      } else {
        lang_ = "en";
      }
      if (lang_) {
        this.$store.commit("setLanguage", lang_);
      }
    }
  }
};
</script>

