import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    language: 'en',
    currentDeviceId: null,
  },
  mutations: {
    setLanguage(state, language) {
      state.language = language
    },
    setCurrentDeviceId(state, deviceId) {
      state.currentDeviceId = deviceId
    },
  },
  actions: {

  }
})
