<script>
import config from "../../config";
import axios from "axios";
import moment from "moment";
import i18n from "../i18n";

export default {
  name: "Home",
  components: {},
  data() {
    return {
      lang: this.$store.state.language,
      deviceId: this.$store.state.currentDeviceId,

      isCurrentDeviceActivated: false,
      queried: false,
      loading: false
    };
  },
  created() {
    if (this.deviceId) {
      this.checkCurrentDevice();
    }
  },
  mounted: function() {
    this.loading = true;
  },
  methods: {
    tran(key) {
      return i18n[this.lang][key];
    },
    timeFormat(t) {
      return moment
        .utc(t)
        .local()
        .format("YYYY-MM-DD HH:mm:ss");
    },
    checkCurrentDevice() {
      let self = this;

      if (!self.deviceId) return;

      self.loading = true;

      const url = `${config.server}/license?id=${self.deviceId}`;

      axios
        .get(url)
        .then(function(response) {
          const { validated } = response.data;
          self.isCurrentDeviceActivated = validated;
        })
        .catch(function(error) {
          console.error(error);
        })
        .finally(function() {
          self.queried = true;
          self.loading = false;
        });
    }
  }
};
</script>

